*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

html {
  box-sizing: border-box;
}

div {
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  padding: 0;
  margin: 0;
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

body {
  font-size: 24px;
  line-height: 32px;
  color: #181818;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 70px;
  overflow: hidden;
}

.container {
  max-width: 1259px;
  padding: 0 15px;
  margin: 0 auto;
}

.section-outer {
  margin-bottom: 120px;
}
.section-outer--small {
  margin-bottom: 100px;
}

.title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  font-size: 55px;
  line-height: 55px;
  text-align: center;
  letter-spacing: 0.01em;
  font-family: "Arno Pro Display", sans-serif;
  font-weight: 300;
}
.title::before, .title::after {
  width: 60vw;
  height: 2px;
  margin: 0 60px;
  content: "";
  background: #dfd7d3;
}
.title::before {
  margin-left: -20vw;
}
.title::after {
  margin-right: -20vw;
}
.title__text {
  width: 100%;
  max-width: fit-content;
}

.btn {
  display: block;
  max-width: 100%;
  padding: 24px 44px;
  cursor: pointer;
  background: none;
  border: none;
}
.btn .btn-text {
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  font-family: "Circle", sans-serif;
  font-weight: 400;
}
.btn-no-bg {
  position: relative;
  min-width: 231px;
  color: #fff;
  border: 2px solid #dbdbdb;
  border-right: none;
  transition: all 0.3s ease-in-out;
}
.btn-no-bg .btn-text {
  position: relative;
}
.btn-no-bg .btn-text::before {
  position: absolute;
  top: 50%;
  bottom: 50%;
  left: calc(100% - -18px);
  width: 66px;
  height: 10px;
  content: "";
  background: url("../images/icons/arrow-right.svg") no-repeat;
  transition: all 0.3s ease-in-out;
  transform: translateY(-50%);
}
.btn-no-bg::before, .btn-no-bg::after {
  position: absolute;
  right: 0;
  width: 2px;
  height: 30%;
  content: "";
  background-color: #dbdbdb;
  transition: all 0.3s ease-in-out;
}
.btn-no-bg::before {
  top: 0;
}
.btn-no-bg::after {
  bottom: 0;
}
.btn-no-bg:hover {
  border-color: #ca6d38;
}
.btn-no-bg:hover::before, .btn-no-bg:hover::after {
  background-color: #ca6d38;
}
.btn-no-bg:hover .btn-text::before {
  left: calc(100% - -30px);
}
.btn-no-bg:active {
  border-color: #dbdbdb;
}
.btn-no-bg:active::before, .btn-no-bg:active::after {
  background-color: #dbdbdb;
}
.btn-no-bg:active .btn-text::before {
  left: calc(100% - -10px);
}
.btn-bg {
  color: #181818;
  background-color: #dfd7d3;
  transition: all 0.3s ease-in-out;
}
.btn-bg:hover {
  box-shadow: 0 0 30px rgba(223, 215, 211, 0.2);
}
.btn-bg:active {
  box-shadow: 0 0 15px rgba(223, 215, 211, 0.1);
}
.btn-standart {
  border: 2px solid #dfd7d3;
  transition: all 0.3s ease-in-out;
}
.btn-standart .btn-text {
  color: #454545;
}
.btn-standart:hover {
  border-color: #ca6d38;
}
.btn-standart:active {
  opacity: 0.8;
}

.tabs-triggers {
  display: flex;
  justify-content: center;
  margin-bottom: 45px;
}
.tabs-triggers__item {
  position: relative;
  font-size: 30px;
  line-height: 32px;
  color: #454545;
  transition: all 0.3s ease-in-out;
  font-family: "Circle", sans-serif;
  font-weight: 300;
}
.tabs-triggers__item::before {
  position: absolute;
  right: 0;
  bottom: -5px;
  left: auto;
  width: 0;
  height: 2px;
  content: "";
  background-color: #ca6d38;
  transition: all 0.3s ease-in-out;
