@media (max-width: 1200px) {
  .contact-phone__text {
    font-size: 20px;
    line-height: 20px;
  }

  .menu__list-item + .menu__list-item {
    margin-left: 20px;
  }

  .title {
    font-size: 50px;
    line-height: 50px;
  }

  .header-contact {
    margin-left: 25px;
  }

  .reviews-item {
    padding: 40px 40px 50px;
  }
  .reviews-slider-photo {
    top: 80px;
    right: 30px;
  }
  .reviews-slider-photo img {
    width: 500px;
    height: 500px;
  }
  .reviews-navigation {
    left: 30px;
  }
}
@media (max-width: 1105px) {
  .logo__img {
    max-width: 200px;
  }

  .contact-phone__text {
    padding-left: 20px;
  }

  .title {
    font-size: 40px;
    line-height: 40px;
  }

  .menu__list-item + .menu__list-item {
    margin-left: 10px;
  }

  .header-contact {
    margin-left: 20px;
  }

  .reviews-slider {
    width: 550px;
    height: 870px;
  }
  .reviews-slider-photo {
    top: auto;
    right: 0;
    bottom: -25%;
    left: 0;
    display: flex;
    justify-content: center;
  }
  .reviews-slider-photo img {
    width: 420px;
    height: 260px;
  }
  .reviews-item {
    min-width: 500px;
    height: 650px;
  }
  .reviews-navigation {
    right: 0;
    left: 0;
    margin: 0 auto;
  }

  .slider-navigation {
    width: 400px;
  }

  .advice-claim {
    max-width: 450px;
  }
}
@media (max-width: 1000px) {
  .burger-btn {
    display: block;
  }

  .header-navigation {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    z-index: 10;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  .intro::before {
    width: 100%;
    height: 55vh;
  }
  .intro-content {
    top: 6%;
    width: 100%;
    transform: translateY(0);
  }

  .gift-img img {
    width: 500px;
    height: 450px;
  }
  .gift__title {
    font-size: 40px;
    line-height: 45px;
  }
  .gift__text {
    font-size: 20px;
    line-height: 25px;
  }

  .advice-inner {
    height: fit-content;
  }
  .advice-content {
    flex-direction: column;
    align-items: center;
  }
  .advice__title {
    max-width: 510px;
    margin: 0 auto;
  }
  .advice-info {
    margin-top: 40px;
  }
}
@media (max-width: 900px) {
  .title {
    font-size: 30px;
    line-height: 30px;
  }
}
@media (max-width: 850px) {
  .preference-top {
    margin-bottom: 25px;
  }
  .preference-items {
    flex-direction: column;
    align-items: center;
  }
  .preference-item {
    text-align: center;
  }
  .preference-item + .preference-item {
    margin-top: 25px;
  }
  .preference-item__img {
    margin-bottom: 15px;
  }
}
@media (max-width: 800px) {
  .title::before, .title::after {
    width: 70vw;
  }

  .footer-inner {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .footer-inner .social-item {
    display: inline-block;
  }
  .footer-item + .footer-item {
    margin-top: 30px;
  }
  .footer-menu {
    flex-direction: column;
    align-items: center;
  }
  .footer-menu__list + .footer-menu__list {
    margin-top: 20px;
  }
  .footer-copyright, .footer-confidentiality {
    display: none;
  }
  .footer-mobile-info {
    display: block;
  }

  .gift-inner {
    flex-direction: column;
    padding-bottom: 50px;
  }
  .gift-info, .gift-img {
    width: 100%;
    margin: 0 auto;
  }
  .gift-img {
    display: inline-block;
    margin-bottom: 30px;
    text-align: center;
  }
  .gift__title {
    font-size: 55px;
    line-height: 55px;
  }
  .gift__text {
    font-size: 24px;
    line-height: 35px;
  }
}
@media (max-width: 700px) {
  .title {
    margin-bottom: 20px;
    font-size: 30px;
    line-height: 30px;
  }
  .title::before, .title::after {
    width: 30vw;
  }

  .preference__text {
    font-size: 20px;
    line-height: 27px;
  }

  .portfolio-content {
    display: block;
  }
  .portfolio__item {
    text-align: center;
  }
  .portfolio__item img {
    width: 100%;
    max-width: 450px;
    height: 100%;
    max-height: 450px;
  }
  .portfolio__item + .portfolio__item {
    margin-top: 10px;
  }
  .portfolio__item:nth-child(6n+1), .portfolio__item:nth-child(6n+2), .portfolio__item:nth-child(6n+3) {
    grid-column: span 1;
  }
  .portfolio__item:nth-child(6n+4), .portfolio__item:nth-child(6n+5), .portfolio__item:nth-child(6n+6) {
    display: none;
    grid-column: span 1;
  }
}
@media (max-width: 600px) {
  .section-outer,
.section-outer--small {
    margin-bottom: 50px;
  }

  .intro::before {
    height: 65vh;
  }
  .intro-content {
    top: 10%;
  }
  .intro__title {
    max-width: 520px;
    font-size: 50px;
    line-height: 50px;
  }
  .intro__btn {
    margin-top: 40px;
  }

  .preference__text {
    font-size: 18px;
    line-height: 26px;
  }

  .gift__title {
    font-size: 40px;
    line-height: 40px;
  }

  .reviews-item {
    min-width: 460px;
    height: 570px;
    padding: 20px 20px 30px;
    font-size: 16px;
    line-height: 24px;
  }
  .reviews-item img {
    width: 350px;
    height: 250px;
  }
  .reviews .title {
    margin-bottom: 25px;
  }
  .reviews-slider {
    width: 480px;
    height: 780px;
  }
  .reviews-slider__title {
    font-size: 20px;
    line-height: 28px;
  }
  .reviews-slider__author {
    margin-top: 22px;
  }

  .slider-navigation {
    width: 350px;
    text-align: center;
  }

  .advice-inner {
    padding: 40px 0;
  }
  .advice__title {
    max-width: 450px;
    font-size: 40px;
    line-height: 44px;
  }
  .advice-form {
    margin-top: 25px;
  }
  .advice-info__items {
    max-width: fit-content;
    margin: 43px auto 0;
  }
  .advice-info__items::before {
    height: 70%;
  }
  .advice-info__item {
    max-width: 350px;
  }
}
@media (max-width: 500px) {
  .btn {
    padding: 18px 23px;
  }
  .btn-no-bg .btn-text::before {
    width: 35px;
    background: url("../images/icons/arrow-right-small.svg");
  }

  .title {
    font-size: 24px;
    line-height: 25px;
  }
  .title::before, .title::after {
    margin: 0 16px;
  }
  .title::before {
    margin-left: -20vw;
  }
  .title::after {
    margin-right: -20vw;
  }

  .reviews-item {
    min-width: 330px;
    height: 515px;
    padding: 15px 15px 25px;
    font-size: 14px;
    line-height: 20px;
  }
  .reviews-item img {
    width: 280px;
    height: 190px;
  }
  .reviews-slider {
    width: 350px;
    height: 700px;
  }

  .slider-navigation {
    width: 290px;
  }

  .swiper-pagination-bullet + .swiper-pagination-bullet {
    margin-left: 5px;
  }

  .advice__title {
    font-size: 25px;
    line-height: 25px;
  }
  .advice-content {
    margin-top: 15px;
  }
  .advice-claim__title {
    font-size: 18px;
    line-height: 26px;
  }
  .advice-info__items::before {
    top: 8%;
    height: 73%;
  }
  .advice-info__item {
    max-width: 254px;
  }
  .advice-info__text {
    margin-left: 15px;
    font-size: 14px;
    line-height: 22px;
  }
  .advice-form__label {
    font-size: 14px;
    line-height: 22px;
  }
  .advice-form__input {
    padding: 14px 20px 14px;
    font-size: 14px;
    line-height: 22px;
    font-family: "Circle", sans-serif;
    font-weight: 300;
  }
  .advice-form__label-checkbox {
    max-width: 150px;
  }
  .advice-btn {
    margin-top: 25px;
    font-size: 18px;
    line-height: 22px;
  }

  .advice-form__item + .advice-form__item {
    margin-top: 20px;
  }
}
@media (max-width: 450px) {
  .btn .btn-text {
    display: block;
    font-size: 14px;
    line-height: 20px;
  }
  .btn-no-bg {
    width: 276px;
  }

  .title::before, .title::after {
    width: 30vw;
  }
  .title::before {
    margin-left: -30vw;
  }
  .title::after {
    margin-right: -30vw;
  }
  .title__text {
    max-width: 165px;
  }

  .tabs-triggers {
    margin-bottom: 25px;
  }
  .tabs-triggers__item {
    font-size: 24px;
    line-height: 26px;
  }
  .tabs-triggers__item + .tabs-triggers__item {
    margin-left: 35px;
  }

  .intro::before {
    height: 55vh;
  }
  .intro__title {
    font-size: 42px;
    line-height: 42px;
  }
  .intro__text {
    font-size: 20px;
    line-height: 26px;
  }

  .preference__text {
    max-width: 350px;
  }

  .portfolio__item img {
    width: 100%;
    max-width: 350px;
    height: 100%;
    max-height: 350px;
  }

  .gift-img img {
    width: 320px;
    height: 300px;
  }
  .gift__title {
    font-size: 24px;
    line-height: 25px;
  }
  .gift__text {
    margin-top: 15px;
    font-size: 18px;
    line-height: 26px;
  }
  .gift__btn {
    margin-top: 25px;
  }
}
@media (max-width: 380px) {
  .btn-no-bg .btn-text::before {
    left: calc(100% - -7px);
  }

  .tabs-triggers__item {
    font-size: 18px;
    line-height: 26px;
  }

  .intro__title {
    max-width: 259px;
    font-size: 30px;
    line-height: 30px;
  }
  .intro__text {
    max-width: 290px;
    margin-top: 15px;
    font-size: 18px;
    line-height: 26px;
  }
  .intro__btn {
    margin-top: 25px;
  }

  .preference__text {
    max-width: 290px;
  }

  .portfolio__item img {
    width: 100%;
    max-width: 290px;
    height: 100%;
    max-height: 290px;
  }

  .reviews-item {
    min-width: 290px;
  }
  .reviews-item img {
    width: 250px;
    height: 170px;
  }
  .reviews-slider {
    width: 300px;
  }
  .reviews-slider__title {
    font-size: 18px;
    line-height: 24px;
  }
  .reviews-slider__text {
    margin-top: 10px;
  }
  .reviews-slider-author {
    margin-top: 15px;
  }

  .slider-navigation {
    width: 270px;
  }

  .advice-btn {
    margin-top: 20px;
    font-size: 14px;
    line-height: 16px;
  }
  .advice-info {
    margin-top: 30px;
  }
  .advice-info__items {
    margin: 40px 0 0 auto;
  }
  .advice-info__items::before {
    height: 71%;
  }
  .advice-info__img {
    width: 30px;
    height: 30px;
  }
}